body {
    padding: 50px;
    font: 14px "Lucida Grande",Helvetica,Arial,sans-serif;
}

a {
    color: #00B7FF;
}
.breadcrumbs {
    display: block;
    margin-bottom: 25px;
}

.breadcrumbs__link {
    position: relative;
    margin-right: 30px;
    text-decoration: none;
    color: #00B7FF;
}

.breadcrumbs__link:last-child {
    color: #000000;
}

.breadcrumbs__link:not(:last-child)::after {
    content: '\2192';
    position: absolute;
    right: -20px;
    pointer-events: none;
}
